/* Oba's Reset/Base */
html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  max-width: 1800px;
  margin: auto;
  position: relative;
}

img,
picture {
  max-width: 100%;
}

button,
input {
  font-family: inherit;
  appearance: none;
}

ul,
ol {
  padding-inline-start: 0;
}

a {
  text-decoration: none;
}

[hidden] {
  display: none !important;
}

/* Add all your variables here */ /* Add your mixins here */
body {
  font-family: -apple-system, BlinkMacSystemFont, "Inter", system-ui, sans-serif;
}

h1,
h2,
h3 {
  font-family: "DM Serif Display", system-ui, serif;
  color: #000;
  font-weight: 400;
}

h1 {
  font-size: clamp(3.5rem, 8vw, 5rem);
  line-height: 1;
  letter-spacing: 1px;
  margin-bottom: 0.3em;
}

h2 {
  font-size: clamp(2.75rem, 4vw, 4rem);
  line-height: 1;
  margin-bottom: 0.3em;
}

h3 {
  font-size: clamp(1.625rem, 2.5vw, 2.25rem);
  font-weight: 500;
  line-height: 1.15;
  margin-bottom: 0.3em;
}

/* List */
p,
li {
  font-size: clamp(1.0625rem, 1.5vw, 1.25rem);
  font-weight: 400;
  line-height: 1.4;
  color: #444;
}

/* Links */
a {
  font-size: inherit;
  color: #0066cc;
  text-decoration: underline 2px transparent;
  transition: text-decoration-color 200ms ease-in-out;
}
a:hover {
  text-decoration-color: #0066cc;
}

figcaption {
  text-align: center;
  font-size: 0.875rem;
  color: #888;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-fluid {
  width: 100%;
}

/******************
    Media Queries - Desktop
*******************/
.button, .button--close, .button--pry {
  appearance: none;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-size: clamp(1.0625rem, 1.5vw, 1.25rem);
  padding: 0.8em 1em;
  text-decoration: none;
  width: fit-content;
}
.button--pry {
  border-radius: 0.5em;
  color: #fff;
  font-weight: 500;
  background: linear-gradient(to right, #000, #434343);
  border: 1px solid transparent;
}
.button--pry i {
  color: #fff;
}
.button--pry:hover {
  background: #000;
}
.button--close {
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 50%;
  color: hsl(37deg, 98%, 53%);
  font-weight: 500;
}

.card {
  max-width: 380px;
}
.card-wrapper {
  display: flex;
  gap: 20px;
}
.card__img {
  background-color: blue;
  height: 120px;
}
.card__content {
  padding: 24px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  gap: 15px;
  justify-content: space-between;
}
.card__title {
  font-size: clamp(2.75rem, 4vw, 4rem);
  margin-bottom: 0;
}
.card__list {
  display: flex;
  gap: 1rem;
  padding: 0;
}
.card__item {
  padding: 2px 5px;
  border-radius: 4px;
  background-color: #e5e5e5;
}
.card__item--active {
  font-weight: bold;
  background-color: lightblue;
}
.card__link {
  background-color: darkblue;
  color: #fff;
  text-decoration: none;
  width: fit-content;
  width: -moz-fit-content;
  padding: 0.6em 1em;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 300ms ease;
}
.card__link:hover {
  background-color: blue;
  color: #fff;
}

/**** Header, Navbar ****/
.header {
  z-index: 100;
  margin: 1em 0;
  background: transparent;
}

.navbar {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 4em;
  padding: 0.5em 0;
}
.navbar__logo {
  max-width: 200px;
}
.navbar__links {
  list-style-type: none;
  display: flex;
  justify-content: space-evenly;
  padding: 0.875em 4px;
  align-items: center;
  width: 100%;
  max-width: 500px;
  background-color: #f5f5f7;
  border-radius: 14px;
  background-color: #f5f5f7;
}
.navbar hr {
  height: 24px;
  border: 1px solid #aaa;
}
.navbar__link {
  font-weight: 400;
  font-family: "DM Serif Display", system-ui, serif;
}
.navbar__link a {
  color: #000;
}

/**** Skip to content ****/
a.skip-link {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
}

.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 40px;
  margin: 1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 80px;
}

/******************
    Media Queries - Desktop
*******************/
.footer {
  border-bottom: 0.75em solid #eee;
  display: flex;
  justify-content: space-between;
  padding: 0.5em 2em;
}
.footer p {
  color: #888;
  font-family: "DM Serif Display", system-ui, serif;
}
.footer em {
  color: #444;
}

.form {
  max-width: 440px;
  padding: 4em 2em;
  background-color: inherit;
}
.form__heading {
  text-align: center;
}
.form__group {
  position: relative;
  min-height: 48px;
  margin-bottom: 1.5em;
}
.form__input {
  background: #fff;
  border: 1px solid hsl(37deg, 98%, 53%);
  width: 100%;
  height: 100%;
  font-size: clamp(1.0625rem, 1.5vw, 1.25rem);
  outline: none;
  padding: 1rem;
  position: absolute;
  top: 0;
  left: 0;
}
.form__label {
  position: absolute;
  left: 1em;
  top: 0.9em;
  background: #fff;
  color: #888;
  font-size: 1rem;
  pointer-events: none;
  z-index: 1;
  transition: all 100ms ease-in;
}
.form__button {
  appearance: none;
  background: linear-gradient(to right, #000, #434343);
  border: 1px solid transparent;
  border-radius: 0.5em;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: clamp(1.0625rem, 1.5vw, 1.25rem);
  font-weight: 500;
  gap: 10px;
  padding: 0.8em 1em;
  text-decoration: none;
  width: 100%;
}
.form__button i {
  background: linear-gradient(to right, #0066cc, #b21f1f, hsl(37deg, 98%, 53%));
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.form__button:hover {
  background: #000;
}

/* Input:focus move up label */
.form__input:focus + .form__label {
  top: -0.5em;
  left: 0.8em;
  font-size: 12px;
  font-weight: 500;
  padding: 2px;
  border-top: 1px solid hsl(37deg, 98%, 53%);
  border-radius: 2px;
  z-index: 1;
}

/*Input:focus sticky top label*/
.form__input:not(:placeholder-shown):not(:focus) + .form__label {
  top: -0.5em;
  left: 0.8em;
  font-size: 12px;
  font-weight: 500;
  padding: 2px;
  border-top: 1px solid hsl(37deg, 98%, 53%);
  border-radius: 2px;
  z-index: 1;
}

/* textarea styling */
.form__textarea {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  padding: 1em;
  border: 1px solid hsl(37deg, 98%, 53%);
  outline: none;
  background: #fff;
}

/* textarea:focus move up label */
.form__textarea:focus + .form__label {
  top: -0.5em;
  left: 0.8em;
  font-size: 12px;
  font-weight: 500;
  padding: 2px;
  border-top: 1px solid hsl(37deg, 98%, 53%);
  border-radius: 2px;
  z-index: 1;
}

/* textarea focus sticky top label */
.form__textarea:not(:placeholder-shown):not(:focus) + .form__label {
  top: -0.5em;
  left: 0.8em;
  font-size: 12px;
  font-weight: 500;
  padding: 2px;
  border-radius: 2px;
  z-index: 1;
}

/*****************
    Common Styles
******************/
.section__container {
  padding: 70px 0 80px;
}
.section__header {
  margin-bottom: 2em;
}
.section__heading, .section__description {
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
}
.section__description {
  color: #666;
}

/*****************
    Hero section
******************/
.hero {
  background-color: #f5f5f7;
  border-radius: 1.25em;
  display: flex;
  padding: 0 1.25em;
  flex-wrap: wrap;
  position: relative;
  height: 540px;
  margin-bottom: 4em;
}
.hero__text_section {
  margin: 2em 1em;
}
.hero__name {
  font-size: clamp(3.5rem, 9vw, 6rem);
  margin-bottom: 0.2em;
}
.hero__position {
  font-size: clamp(1.625rem, 2.5vw, 2.25rem);
  letter-spacing: -1px;
  margin-bottom: 0.5em;
  background: linear-gradient(to right, #1a2a6c, #b21f1f, hsl(37deg, 98%, 53%));
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.hero__social_links {
  font-size: 18px;
  color: #888 !important;
}
.hero__image_section {
  position: absolute;
  bottom: 0;
  right: -20px;
  user-select: none;
  pointer-events: none;
}
.hero__image_section img {
  width: 100%;
  margin-bottom: -5px;
  height: 100%;
}

/*****************
    About Section
******************/
.about {
  background-color: #f5f5f7;
}
.about__allcards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1em;
}
.about__card {
  background-color: #fff;
  border-radius: 10px;
  padding: 3em 2.25em;
  flex: 1 1 300px;
}
.about__card_icon {
  background-color: #f5f5f7;
  border-radius: 5px;
  padding: 4px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about__card_icon i {
  font-size: 1.5rem;
  background: linear-gradient(#1a2a6c, #b21f1f, hsl(37deg, 98%, 53%));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.about__card__heading {
  margin-top: 2em;
}

/*****************
    Blog Section
******************/
.blog__allcards {
  display: flex;
  justify-content: space-between;
  gap: 1em;
  flex-wrap: wrap;
}
.blog__card {
  border-radius: 1em;
  display: grid;
  flex: 1 1 325px;
  padding: 2.5em 2em;
}
.blog__card:hover {
  background-color: #f5f5f7;
}
.blog__card__heading {
  color: #444;
}
.blog__card ul {
  padding: 1em 0;
}
.blog__card__post_title {
  list-style: none;
  margin-bottom: 1em;
}
.blog__card__post_title a {
  color: #222;
}
.blog__card__post_title a:hover {
  color: #0066cc;
}
.blog__card__more_link {
  justify-self: baseline;
}

/*****************
    Projects Section
******************/
.projects {
  background-color: #000;
}
.projects .section__heading {
  color: #fff;
}
.projects .section__description {
  color: #ccc;
}
.projects__allcards {
  display: flex;
  justify-content: space-between;
  gap: 2em;
  flex-wrap: wrap;
}
.projects__card {
  flex: 1 1 325px;
  padding: 3em 2em;
  border-radius: 10px;
  position: relative;
  box-shadow: 5px 5px 10px rgba(255, 255, 255, 0.05);
  background-color: #1d1d1f;
  border: 2px solid transparent;
  transition: border-color 200ms ease-in-out;
}
.projects__card__heading {
  color: #f5f5f7;
  margin-bottom: 0.5em;
}
.projects__card__badges {
  display: flex;
  gap: 0.5em;
  margin-bottom: 0.75em;
}
.projects__card__badges span {
  font-size: 0.75rem;
  border-radius: 4px;
  padding: 6px 10px;
  border-radius: 4px;
  background-color: #2d2d2f;
  color: #f5f5f7;
}
.projects__card__desc {
  margin-bottom: 1em;
  color: #f5f5f7;
}
.projects__card__link {
  color: #2997ff;
}
.projects__card:after {
  content: "";
  clip-path: polygon(0% 0%, 100% 0, 100% 100%, 50% 75%, 0% 100%);
  color: hsl(37deg, 98%, 53%);
  position: absolute;
  top: 0;
  right: 2em;
  width: 32px;
  height: 40px;
}
.projects__card:first-child::after {
  background: linear-gradient(#093637, #44a08d);
}
.projects__card:nth-child(2)::after {
  background: linear-gradient(#000468, #4b6cb7);
}
.projects__card:hover {
  border-color: #fff;
}

/*****************
    Contact section
******************/
.contact {
  background-color: #f5f5f7;
  border-radius: 1.25em;
  margin-top: 3em;
  margin-bottom: 2em;
}
.contact__form-container {
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
  border-radius: 1em;
  justify-content: space-between;
  max-height: 600px;
  overflow: hidden;
}
.contact__form-group {
  padding: 2em 1.5em;
  flex: 1 1 400px;
}
.contact__form-image {
  display: none;
}
.contact__form-control {
  display: flex;
  flex-direction: column;
  gap: 6px;
  border-radius: 0.5em;
  position: relative;
  height: 56px;
  margin-bottom: 1em;
}
.contact__form-control-textarea {
  height: auto;
}
.contact__form-control label {
  background-color: #fff;
  color: #444;
  font-size: 1rem;
  font-weight: 500;
  padding: 4px;
  pointer-events: none;
  position: absolute;
  top: 0.875em;
  left: 1.2em;
  transition: all 200ms ease;
}
.contact__form-control input, .contact__form-control textarea {
  appearance: none;
  border: 1px solid #aaa;
  border-radius: 0.375em;
  height: 100%;
  font-size: clamp(1.0625rem, 1.5vw, 1.25rem);
  font-family: inherit;
  outline: none;
  padding-left: 1em;
  padding-top: 0.7em;
  transition: all 180ms ease;
}
.contact__form-control textarea {
  height: 200px;
  width: 100%;
  padding-top: 1.5em;
  padding-right: 1em;
}
.contact__form-control input:hover, .contact__form-control textarea:hover {
  box-shadow: 0 0 0 5px #eee;
}
.contact__form-control input:focus, .contact__form-control textarea:focus {
  outline: hsl(228deg, 61%, 26%);
  box-shadow: 0 0 0 4px #ccc;
}
.contact__form-control input:focus + label, .contact__form-control textarea:focus + label {
  font-size: 12px;
  top: 0.3em;
}
.contact__form-control input:not(:placeholder-shown) + label, .contact__form-control textarea:not(:placeholder-shown) + label {
  font-size: 12px;
  top: 0.3em;
  color: #888;
}

/*****************
    Media Queries - Desktop
******************/
/* Tablet & Computer */
@media (min-width: 800px) {
  .hero {
    align-items: center;
    height: 400px;
    padding: 0 4em;
  }
  .contact {
    min-height: 300px;
  }
  .contact__form-image {
    display: inline;
    max-height: 400px;
    flex: 1 1 300px;
  }
  .contact__form-image img {
    object-fit: cover;
    height: 100%;
    width: 100%;
    object-position: bottom center;
  }
}
/* Large Desktop */
@media (min-width: 1200px) {
  .hero {
    height: 520px;
  }
  .hero__image_section {
    width: 55%;
  }
}